.event-list {
  flex-flow: wrap;
  justify-content: center;
  width: 100vw;
  padding-bottom: 4em;
  display: flex;
}

.install-wrapper {
  opacity: 1;
  z-index: 1000;
  background-color: #d3d3d3;
  border-radius: 5px;
  margin: 2em 0;
  padding: 1em;
  font-size: 1.2em;
  position: fixed;
}

.install-list-item {
  align-items: center;
  height: 2em;
  display: flex;
}

.install-button-wrapper {
  justify-content: center;
  margin-top: 2em;
  display: flex;
}

@media only screen and (width <= 600px) {
  .install-wrapper {
    width: 70vw;
  }
}

.event-tile {
  border-radius: 12px;
  grid-template-columns: 100%;
  display: grid;
}

.event-header-title {
  justify-content: space-between;
  align-items: center;
  height: 2em;
  font-size: 18px;
  font-weight: 500;
  display: flex;
}

.event-header-title-left {
  font-size: 1.2em;
}

.event-details {
  flex-direction: row;
  display: flex;
}

.event-details-container {
  padding: .5em 0 0 .5em;
}

.event-details-icons {
  flex-direction: column;
  justify-content: space-around;
  min-width: 42px;
  padding-top: .5em;
  display: flex;
}

.event-details-title {
  margin: 0;
  font-size: 1.5em;
}

.event-details-footer {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.event-details-footer-button {
  background: var(--tertiary-color);
  border: 1px solid var(--tertiary-color);
  cursor: pointer;
  color: var(--secondary-color);
  border-radius: 4px;
  max-width: 11em;
  min-height: 2em;
  padding: 0 .5em;
  font-size: .9em;
  text-decoration: none;
  display: inline-block;
}

.event-details-description {
  align-items: center;
}

.event-details-description-more {
  background-color: var(--area-color);
  color: var(--tertiary-color);
  border: none;
}

.event-details-favorites-svg {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
}

.favorited {
  fill: gold;
  color: gold;
  filter: drop-shadow(1px 2px 1px #0006);
}

@media only screen and (width <= 600px) {
  .event-tile {
    grid-template-rows: 2.5em auto;
    width: 90vw;
    height: auto;
    margin: 10px auto;
    padding: .8em;
  }

  .event-details {
    max-width: 89vw;
  }

  .event-details-icons {
    font-size: 1.3em;
  }

  .event-details-fav {
    font-size: 1.5rem;
  }

  .event-details-footer {
    min-height: 1.6em;
    font-size: 1.1em;
    font-weight: 400;
  }

  .event-details-footer-area {
    padding: .3em;
  }

  .event-details-footer-where {
    max-width: 50%;
  }

  .event-details-description {
    min-height: 2em;
    margin: 0;
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: 1.2em;
    font-weight: 200;
  }

  .event-details-description-more {
    font-size: 1em;
  }

  .event-details-favorites-svg {
    width: 1em;
    height: 1em;
    font-size: 1.3rem;
  }
}

@media only screen and (width >= 600px) {
  .event-tile {
    grid-template-rows: 2.5em auto;
    width: 43vw;
    max-width: 22em;
    height: auto;
    margin: 10px 5px;
    padding: .8em;
  }

  .event-details-icons {
    font-size: 1.3em;
  }

  .event-details-fav {
    font-size: 1.5rem;
  }

  .event-details-footer {
    min-height: 1.6em;
    font-size: 1.1em;
    font-weight: 400;
  }

  .event-details-footer-area {
    padding: .3em;
  }

  .event-details-footer-where {
    max-width: 50%;
  }

  .event-details-description {
    min-height: 2em;
    padding-top: .7em;
    padding-bottom: .4em;
    font-size: 1.2em;
    font-weight: 200;
  }

  .event-details-description-more {
    font-size: 1em;
  }

  .event-details-favorites-svg {
    width: 1em;
    height: 1em;
    font-size: 1.3rem;
  }
}

.menu-favorites-svg {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
  font-size: 1.5rem;
}

.menu-header-wrapper {
  z-index: 1000;
  height: 4em;
  position: sticky;
  top: 0;
  box-shadow: 0 -10px 7px 7px #000;
}

.menu-header-content {
  cursor: pointer;
  background-color: var(--primary-color);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 100vw;
  height: 4em;
  padding: 0 .7em;
  display: flex;
}

.menu-header-icon-wrapper {
  align-items: center;
  display: flex;
}

.menu-header-svg {
  width: 2rem;
  height: 2rem;
  margin: .2em;
  font-size: 1.5rem;
  transition: transform .3s;
}

.menu-accordian-drawer {
  background-color: var(--primary-color);
  border-radius: 0 0 5px 5px;
  flex-direction: column;
  justify-content: space-between;
  min-height: 10em;
  display: flex;
}

.menu-accordian-contact {
  border: none;
  margin-top: 1em;
  font-size: 1em;
}

.menu-accordian-links {
  justify-content: space-between;
  display: flex;
}

.menu-filter-container {
  flex-direction: column;
  display: flex;
}

.menu-filter-title {
  justify-content: center;
  align-items: center;
  padding-top: .5em;
  display: flex;
}

.menu-filter-label {
  justify-content: space-between;
  align-items: center;
  margin: .3em 0;
  font-size: 1.2em;
  display: flex;
}

.menu-filter-switch {
  width: 2.6em;
  height: 1.2em;
  display: inline-block;
  position: relative;
}

.menu-filter-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.menu-filter-slider {
  cursor: pointer;
  background-color: #ccc;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.menu-filter-slider:before {
  content: "";
  background-color: #fff;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  transition: all .4s;
  position: absolute;
  bottom: .1em;
  left: .1em;
}

input:checked + .menu-filter-slider {
  background-color: #2196f3;
}

input:focus + .menu-filter-slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .menu-filter-slider:before {
  transform: translateX(26px);
}

.expanded {
  transform: rotate(90deg);
}

.closed {
  transform: rotate(180deg);
}

.search-input-wrapper > input {
  border: 1px solid #000;
  border-radius: 20px;
  font-size: 2em;
}

@media only screen and (width <= 600px) {
  .menu-accordian-drawer {
    width: 100vw;
    max-width: 40em;
    margin: 0 auto;
    padding: 0 0 1em;
  }

  .menu-accordian-contact {
    padding: 0 1rem;
  }

  .menu-filter-container {
    width: 80vw;
    margin: 0 auto;
  }

  .menu-filter-label {
    width: 80vw;
  }

  .search-input-wrapper > input {
    width: 60vw;
    padding: 0 .5em;
  }
}

@media only screen and (width >= 600px) {
  .menu-accordian-drawer {
    width: 40vw;
    max-width: 40em;
    margin: 0 .5em 0 auto;
    padding: 1em;
  }

  .menu-filter-container, .menu-filter-label {
    width: 40vw;
    max-width: 40em;
  }

  .search-input-wrapper > input {
    width: 80vw;
    margin: 0 auto;
    padding: 0 .5em;
  }
}

.footer-tab-wrapper {
  background-color: var(--primary-color);
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 3em;
  display: flex;
  position: fixed;
  bottom: 0;
  box-shadow: 0 10px 7px 7px #000;
}

.footer-tabs {
  font-family: Futura Bold, sans-serif;
  font-size: 1em;
  font-variant-caps: small-caps;
}

.footer-tabs > button {
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 1em;
}

.footer-tab-active {
  background-color: var(--tertiary-color);
  color: var(--primary-color);
  border-radius: 10px;
  font-family: Futura Bold, sans-serif;
  font-size: 1em;
  font-variant-caps: small-caps;
}

.footer-tab-active > button {
  color: var(--primary-color);
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 1.5em;
}

@font-face {
  font-family: Futura Bold;
  src: url("Futura Bold.2013bcc4.otf") format("opentype");
  font-family: Futura Bold;
  src: url("Futura Bold.2013bcc4.otf") format("opentype");
}

:root {
  --primary-color: #d7d7d7;
  --secondary-color: #e2f7e4;
  --tertiary-color: #496d2e;
  --fav-color: #ffc35c;
  --backdrop-color: #000;
}

* {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

html, body {
  background-color: var(--backdrop-color);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
